.page {
  background-color: #262626;
  height: 100vh;
}
.animation-area {
    position: fixed;
	/* background: linear-gradient(to left, #9a989b0a, #e0e0e002); */
	width: 100%;
	height: 100vh;
    z-index: -1;
}

.box-area {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
    
}
.box-area li {
	position: absolute;
	display: block;
	list-style: none;
	width: 25px;
	height: 25px;
	background: #45e1fd60;
	animation: animate 20s linear infinite;
	bottom: -100px;
}
.box-area li:nth-child(1) {
	left: 86%;
	width: 80px;
	height: 80px;
	animation-delay: 0s;
    background: #f78a8a50;
}
.box-area li:nth-child(2) {
	left: 12%;
	width: 30px;
	height: 30px;
    border-radius: 50%;
	animation-delay: 1.5s;
	animation-duration: 10s;
    background: #4bff7850;
}
.box-area li:nth-child(3) {
	left: 70%;
	width: 100px;
	height: 100px;
	animation-delay: 5.5s;
    background: #fd75f250;
}
.box-area li:nth-child(4) {
	left: 42%;
	width: 150px;
	height: 150px;
    border-radius: 50%;
	animation-delay: 0s;
	animation-duration: 15s;
    background: #f3f18950;
}
.box-area li:nth-child(5) {
	left: 65%;
	width: 40px;
	height: 40px;
	animation-delay: 0s;
    background: #1600df50;
}
.box-area li:nth-child(6) {
	left: 15%;
	width: 110px;
	height: 110px;
	animation-delay: 3.5s;
}
@keyframes animate {
	0% {
		transform: translateY(0) rotate(0deg);
		opacity: 1;
	}
	100% {
		transform: translateY(-1000px) rotate(360deg);
		opacity: 0;
	}
}